<template>
    <div class="flex-col">
        <div class="button-group">
            <el-button
                    type="primary"
                    icon="el-icon-circle-plus"
                    @click="handleAdd">新增
            </el-button>
        </div>
        <v-table :size="params.size" :page.sync="params.page"
                 :data="data" :total="total" :loading="false">
            <el-table-column prop="name" label="角色" align="center"></el-table-column>
            <el-table-column label="创建时间" align="center">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    {{ scope.row.createDate }}
                </template>
            </el-table-column>
            <el-table-column prop="status" label="状态" align="center">
                <template slot-scope="scope">
                    {{translator(scope.row.status,roleStatusMap)}}
                </template>
            </el-table-column>
            <el-table-column prop="remarks" label="备注" align="center"></el-table-column>
            <el-table-column label="操作" align="center" width="300">
                <template slot-scope="scope">
                    <el-button
                            plain
                            type="info"
                            icon="el-icon-view"
                            @click="handleView(scope.row)">查看
                    </el-button>
                    <el-button
                            plain
                            type="primary"
                            icon="el-icon-edit"
                            @click="handleEdit(scope.row)">编辑
                    </el-button>
                    <el-button
                            plain
                            :type="!!scope.row.status ? 'warning' : 'success'"
                            :icon="!!scope.row.status ? 'el-icon-remove-outline' : 'el-icon-circle-check-outline'"
                            @click="handleStatus(scope.row)">
                        {{!!scope.row.status ? '禁用' : '启用'}}
                    </el-button>
                </template>
            </el-table-column>
        </v-table>

        <!--dialog 弹层-->
        <el-dialog v-el-drag-dialog :title="dialogTitle" center :visible.sync="dialog"
                   width="460px" @close="closeDialogCallBack"
        >
            <el-form :model="formData" ref="form" status-icon :rules="rules" label-width="85px"
                     @keyup.enter.native="submitForm">

                <el-form-item label="角色名称:" prop="name">
                    <el-input v-model="formData.name" placeholder="请输入姓名" auto-complete="off"
                              :disabled="isView"></el-input>
                </el-form-item>

                <el-form-item label="角色权限:" prop="authority">
                    <el-tree
                            :data="menuList"
                            show-checkbox
                            empty-text="菜单树为空或后台服务请求失败"
                            node-key="id"
                            ref="tree"
                            @check="changeMenuCheck"
                            :default-checked-keys="checkedList"
                            :props="treeProps">
                    </el-tree>
                </el-form-item>

                <el-form-item label="备注:" prop="notes">
                    <el-input
                            v-model="formData.notes"
                            type="textarea"
                            :disabled="isView"
                            :autosize="{ minRows: 3, maxRows: 3}"
                            placeholder="备注">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer">
                <el-button @click="dialog = false">取 消</el-button>
                <el-button type="primary" v-show="!isView" @click="submitForm">确 定</el-button>
            </div>
        </el-dialog>

    </div>
</template>

<script type="text/ecmascript-6">
    import vTable from '@/components/vTable'
    import {logic, translator, dataMapping, resetFormData} from "@/utils";
    import {$SYS_ERR, statusMap} from "@/config/dictionary";
    import {apiRoleList} from "@/api";

    export default {
        name: 'roleManage',
        data() {
            let checkAuthority = (rule, value, callback) => {
                this.$nextTick(()=>{
                    console.log(this.formData.authority);
                    if(this.formData.authority.length){
                        callback();
                    }else{
                        callback(new Error('至少选择一项'));
                    }
                });
            };
            return {
                // table列表相关
                params: {
                    size: 100,
                    page: 1
                },
                roleStatusMap: statusMap,
                translator: translator,
                // 弹层相关
                dialogTitle: '',
                dialog: false,
                isView: false,
                formData: {
                    name: '',
                    authority: [],
                    notes: ''
                },
                rules: {
                    name: [
                        {required: true, message: '请输入角色名', trigger: 'blur'},
                    ],
                    authority: [
                        {required: true, validator: checkAuthority, trigger: 'change'}
                    ]
                },
                checkedList: [],
                rowData: null,
                // 树形菜单列表
                menuList: [
                    {
                        id: '1',
                        label: '用户管理',
                        children: [
                            {
                                id: '1-1',
                                label: '前台用户',
                                children: [
                                    {
                                        id: '1-1-add',
                                        label: '增加'
                                    }, {
                                        id: '1-1-view',
                                        label: '查看'
                                    }, {
                                        id: '1-1-edit',
                                        label: '编辑'
                                    }, {
                                        id: '1-1-delete',
                                        label: '删除'
                                    }]
                            },
                        ]
                    },
                    {
                        id: '2',
                        label: '商户管理',
                        children: [
                            {
                                id: '2-1',
                                label: '商户列表',
                                children: [{
                                    id: '2-1-add',
                                    label: '增加'
                                }, {
                                    id: '2-1-view',
                                    label: '查看'
                                }, {
                                    id: '2-1-edit',
                                    label: '编辑'
                                }, {
                                    id: '2-1-delete',
                                    label: '删除'
                                }]
                            },
                            {
                                id: '2-2',
                                label: '商户审核',
                                children: [{
                                    id: '2-2-add',
                                    label: '增加'
                                }, {
                                    id: '2-2-view',
                                    label: '查看'
                                }, {
                                    id: '2-2-edit',
                                    label: '编辑'
                                }, {
                                    id: '2-2-delete',
                                    label: '删除'
                                }
                                ]
                            }
                        ]
                    },
                    {
                        id: '3',
                        label: '角色管理',
                        children: [
                            {
                                id: '3-add',
                                label: '增加'
                            }, {
                                id: '3-view',
                                label: '查看'
                            }, {
                                id: '3-edit',
                                label: '编辑'
                            }, {
                                id: '3-delete',
                                label: '删除'
                            }
                        ]
                    },
                    {
                        id: '4',
                        label: '商品列表',
                        children: [
                            {
                                id: '4-add',
                                label: '增加'
                            }, {
                                id: '4-view',
                                label: '查看'
                            }, {
                                id: '4-edit',
                                label: '编辑'
                            }, {
                                id: '4-delete',
                                label: '删除'
                            }
                        ]
                    },
                    {
                        id: '5',
                        label: '资金流水',
                        children: [
                            {
                                id: '5-view',
                                label: '查看'
                            }
                        ]
                    }
                ],
                treeProps: {
                    children: 'children',
                    label: 'label'
                }
            }
        },
        computed: {
            data() {
                return this.$store.state.other.roleList;
            },
            total() {
                return this.data.length;
            }
        },
        watch: {
            // checkedList(n, o) {
            //     this.formData.authority = this.checkedList.concat();
            // }
        },
        methods: {
            /**
             * 新增角色
             */
            handleAdd() {
                this.dialogTitle = '新增';
                this.openDialog();
            },
            /**
             * 查看角色详情
             */
            handleView(row) {
                this.dialogTitle = '查看';
                this.isView = true;
                this.rowData = row;
                this.openDialog();
            },
            /**
             * 编辑角色详情
             */
            handleEdit(row) {
                this.dialogTitle = '编辑';
                this.rowData = row;
                this.openDialog();
            },
            /**
             * 切换用户状态
             */
            handleStatus(row) {
                let message = '';
                !!row.status ? message = '禁用' : message = '启用';
                this.$confirm('确定要' + message + '该角色？', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    row.status = !row.status;
                    this.$notify({
                        title: '提示',
                        message: message + '成功',
                        type: 'success',
                        duration: 800,
                        showClose: false
                    });
                });
            },
            /**
             * 打开弹层
             */
            openDialog() {
                this.dialog = true;
                this.$nextTick(() => {
                    dataMapping(this.formData, this.rowData);
                    this.checkedList = this.formData.authority;
                })
            },
            /**
             * 关闭弹层回调
             */
            closeDialogCallBack() {
                setTimeout(() => {
                    this.isView = false;
                    this.rowData = null;
                    this.$refs.tree.setCheckedKeys([]);
                    this.$refs['form'].resetFields();
                    this.checkedList = this.formData.authority;
                }, 100);
            },
            /**
             * 改变菜单权限时触发
             */
            changeMenuCheck(tar, all) {
                this.formData.authority = [...all.checkedKeys];
                this.checkedList = this.formData.authority;
            },
            // 提交表单
            submitForm() {
                this.$refs['form'].validate((valid) => {
                    if (valid) {
                        this.dialog = false;
                        this.$notify({
                            title: '提示',
                            message: '提交成功',
                            type: 'success',
                            duration: 800,
                            showClose: false
                        });
                    }
                });
            }
        },
        components: {
            vTable
        }
    }
</script>

<style lang="scss" type="text/scss">
    .el-tree__empty-block {
        min-height: 28px;
        text-align: left;
    }

    .el-tree__empty-text {
        color: red;
        width: 100%;
    }
</style>
