<template>
    <div class="v-table" v-loading="loading">
        <el-table
                stripe
                :data="data"
        >
            <el-table-column label="序号" type="index" :index="getIndex" align="center"></el-table-column>
            <slot></slot>
        </el-table>
        <el-pagination
                @current-change="currentChange"
                :current-page="page"
                :page-size="size"
                :layout="layout"
                :total="total">
        </el-pagination>
    </div>
</template>

<script type="text/ecmascript-6">
    export default {
        name: 'vTable',
        props: {
            data: {
                type: Array,
                default: function () {
                    return []
                }
            },
            total: {
                type: Number,
                default: 0
            },
            loading: {
                type: Boolean,
                default: true
            },
            page: {
                type: Number,
                default: 1
            },
            layout: {
                default: 'total, prev, pager, next, jumper'
            },
            size: {
                type: Number,
                default: 10
            }
        },
        data() {
            return {}
        },
        methods: {
            currentChange(val) {
                // 更新父组件的page参数，执行查找接口
                this.$emit('update:page',val);
                this.$emit('pageChage');
            },
            getIndex(index){
                return (this.page - 1) * this.size + index + 1;
            }
        },
    }
</script>

